import Modal from './Modal';

/**
 * Modal Component.
 * React wrapper for a Modal Window. Allows to dynamically toggle a Modal window.
 *
 * ================================================
 * Usage:
 *
 * =================================================
 *
 */

class FormModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = { show: false, throwWarning: false }

    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.confirmClose = this.confirmClose.bind(this);
    this.setWarning = this.setWarning.bind(this);
  }

  renderChildren() {
    return React.Children.map(this.props.children, child => {
      return React.cloneElement(child, { onSuccess: this.closeModal, onChange: this.setWarning });
    });
  }

  openModal() {
    this.setState({
      show: true
    });
  } 

  setWarning() {
    this.setState({
      throwWarning: true
    });
  }

  confirmClose() {
    if (this.state.throwWarning) {
      swal({
        title: "Are You Sure?",
        text: "Leaving the form will result in the loss of any information entered.",
        type: "warning",
        showCancelButton: true,
        confirmButtonText: 'Proceed',
        cancelButtonText: 'Cancel',
      },
      function() {
        this.closeModal();
      }.bind(this));
    } else {
      this.closeModal();
    }
  }

  closeModal() {
    this.setState({
      show: false,
      throwWarning: false
    });
  }

  render() {
    return (
      <Modal 
        closeModal={this.confirmClose} 
        openModal={this.openModal} 
        show={this.state.show}
        {...this.props} 
      >
        {this.renderChildren()}
      </Modal>
    );
  }
}

FormModal.propTypes = {
  title: React.PropTypes.string,
  buttonClass: React.PropTypes.string,
  buttonStyle: React.PropTypes.object,
  buttonContent: React.PropTypes.object,
};
FormModal.defaultProps = {
  buttonClass: 'btn btn-primary'
};

export default FormModal;



