/**
 * Modal Component.
 * React wrapper for a Modal Window. Allows to dynamically toggle a Modal window.
 *
 * ================================================
 * Usage:
 *
 * =================================================
 *
 */
class Modal extends React.Component {

  constructor(props) {
    super(props);

  }

  render() {
    // Black Background with Alpha Channel
    const backdropStyle = {
      position: 'fixed',
      zIndex: 9998,
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.3)',
      padding: 50
    };

    // White Modal Window
    const modalStyle = {
      position: 'relative',
      maxWidth: 1000,
      maxHeight: '100%',
      margin: '0 auto',
      backgroundColor: '#fff',
      borderRadius: 10,
      overflowY: 'auto',
      zIndex: 9999
    };

    const titleStyle = {
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      background: '#093782',
      height: '70px',
      borderTopRightRadius: '10',
      color: '#FFFFFF',
      fontSize: 24,
      padding: 20,
    }

    const glyphStyle = {
      marginLeft: 'auto',
      cursor: 'pointer'
    }

    const bodyStyle = {
      padding: 15
    }
  
    var button = (
      <button
        type='button'
        className={this.props.buttonClass}
        onClick={this.props.openModal}
        style={this.props.buttonStyle}
      >
        {this.props.buttonContent}
      </button>
    );

    var modal;
    if (this.props.show) {
      modal = (
        <div style={backdropStyle} onClick={this.props.closeModal}>
          <div
            style={modalStyle}
            onClick={e => {
              e.stopPropagation();
            }}
          >
            <div
              style={titleStyle}
            >
              {this.props.title}
              <span 
                className='glyphicon glyphicon-remove'
                style={glyphStyle}
                onClick={this.props.closeModal}
              />
            </div>
            <div
              style={bodyStyle}
            >
              {this.props.children}
            </div>
          </div>
        </div>
      );
    }

    return (
      <div>
        {button}
        {modal}
      </div>
    );
  }
}

Modal.propTypes = {
  title: React.PropTypes.string,
  buttonClass: React.PropTypes.string,
  buttonStyle: React.PropTypes.object,
  buttonContent: React.PropTypes.object,
  show: React.PropTypes.boolean,
  closeModal: React.PropTypes.func,
  openModal: React.PropTypes.func
};
Modal.defaultProps = {
  buttonClass: 'btn btn-primary'
};

export default Modal;



